
<template>
  <div>
     <div class="box">
      <swiper class="swiper" :options="swiperOption"> 
          <swiper-slide><img src="../assets/main-img1.jpg" alt=""></swiper-slide>
          <swiper-slide><img src="../assets/guide_img01.jpg" alt=""></swiper-slide>
          <swiper-slide><img src="../assets/guide_img02.jpg" alt=""></swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
     </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'App',
  components: {
      Swiper,
      SwiperSlide
  },
   data() {
      return {
        swiperOption: {
          slidesPerView: 1,
          spaceBetween: 30,
          loop: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        }
      }
    }
}
</script>

<style>


img{width: 100%;}
 .swiper {
    height: 100%;
    width: 100%;
 }
  
 .swiper .swiper-slide {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-weight: bold;
  }

</style>