<!-- 메인 페이지 마크업 -->
<template lang="html">
    <div class="box1">
      <h3>착한 가게 리스트</h3>
      <div class="search">
        <label for="title">지역 검색하기 </label>
        <input id="title" type="text" v-model="value" />
        <button @click="search()">검색</button>
      </div>

      <div v-show="message">{{msgtext}}</div>

      <div class="tabs">
        <button type="button" @click="aaa(0)">전체보기</button>
         <button type="button" @click="aaa(1)">한식</button>
        <button type="button" @click="aaa(2)">경양식/일식</button>
        <button type="button" @click="aaa(3)">중식</button>
        <button type="button" @click="aaa(4)">세탁업</button>
        <button type="button" @click="aaa(5)">미용업</button>
      </div>

      <ul class="StoreList">
            <li v-for="(item, ind) in StoreList" :key="ind">
               <img :src="item.sh_photo" alt="" class="rimg">
                <div class="name">
                    <p>{{ item.sh_name }}</p>
                    <span>
                        <i class="fa-solid fa-location-dot"></i>
                       {{ item.sh_pride }} 
                    </span>
                    <span>
                        <i class="fa-solid fa-location-dot"></i>
                       {{  item.sh_addr  }} 
                    </span>
                </div>
                <div class="tel">
                  <p> tel {{ item.sh_phone }}</p>
                </div>
            </li>
        </ul>
    </div>
  </template>
  
  <script>
  import jdata from '../json/seoul.json';
  import jdata1 from '../json/koreanFood.json';
  import jdata2 from '../json/japanFood.json';
  import jdata3 from '../json/chinaFood.json';
  import jdata4 from '../json/wash.json';
  import jdata5 from '../json/beautyStore.json';

  export default {
    data(){
      return{
        message:false,
        msgtext:'검색 데이터가 없습니다',
        value: '',
        StoreList : jdata,
        ary:[jdata ,jdata1, jdata2, jdata3, jdata4, jdata5]
        
      }
    },
    methods:{
      search: function(){
        let text = this.value;
        if(text){
          this.StoreList = jdata.filter(function(element){
            return element.sh_addr.includes(text);  //조건에 참이면 객체를 배열로 리턴
          });
          
          if(this.StoreList.length==0){
             this.message=true;
             this.msgtext='검색 데이터가 없습니다';
             
          }else{
             this.message=true;
             this.msgtext=this.StoreList.length + '개가 검색되었습니다';
             
          }
        }else{
          alert('검색어를 입력해주세요!');
        } 
      },
      aaa: function(aa){
        // if(aa==0){
        //   this.oneromms = jdata;
        // }else if(aa==1){
        //   this.oneromms = jdata2;
        // }else if(aa==2){
        //   this.oneromms = jdata3;
        // }
        this.StoreList = this.ary[aa];
      }
    }

  }
  </script>
  
  <style>
    .search label{font-size: 1.453rem; margin-right: 20px;}
    .search button{width: 100px; height: 50px; border: 1px solid #ccc; margin-left: 20px;}
    .search input{width: 300px; height: 50px;border: 1px solid #ccc;}
    .tabs{width: 100%; margin: 100px 0;}
    .tabs button{width: 14.88%; border: 1px solid #ccc; font-size: 1.564rem; padding: 20px 0;}
    .tabs button:hover{background: #333; color: #fff; transition: .6s all ease-out;}
 .StoreList_Wrap{padding:2rem;}
    .StoreList_Wrap h2{color:#35495e; letter-spacing:-0.025rem; font-size: 1.125rem;}
    .StoreList_Wrap h2 i{padding-right:10px;}
    .StoreList_Wrap h2 b{font-weight: 700;}

    .StoreList img{width: 200px; height: 200px;}
    .storeSearch{border:3px solid #35495e; margin:1rem 0 0;}
    .storeSearch form{display:flex; justify-content: space-between; padding:0 1rem;}
    .storeSearch input,
    .storeSearch button{height: 3rem; line-height: 3rem; outline: none;}
    .storeSearch input{flex:1 1 auto; border:0 none;}
    .storeSearch button{flex:0 0 1rem; color:#35495e; border:0 none; background:none;}

    .StoreList{border-top:1px solid #ccc; margin:2rem 0 0;}
    .StoreList li{display:flex; justify-content: space-between; border-bottom:1px solid #eee; padding:2rem 0;}
    .StoreList li .name{flex:1 1 auto;}
    .StoreList li .name p{font-weight:700; font-size:1.25rem;}
    .StoreList li .name span{display: block; margin:1rem 0 0; color:#888;}
    .StoreList li .name span i{color:#333; padding-right:5px;}
    .StoreList li .tel{flex:0 0 9em; align-self: center; color:#42b983; text-align:right;}
    .StoreList li .tel i{padding-right:5px; font-size: .8rem;}

    @media only screen and (max-width:768px) {
        .StoreList li{flex-wrap: wrap; line-height: 1.6;}
        .StoreList li .idx{display: none;}
        .StoreList li .name{flex-basis:100%;}
        .StoreList li .name span{position:relative; padding-left:22px;}
        .StoreList li .name span i{position:absolute; left:0; top:6px;}
        .StoreList li .tel{flex-basis:100%; position:relative; text-align:left; margin:.5rem 0 0; padding-left:22px;}
        .StoreList li .tel i{position:absolute; left: 0; top: 8px;}
    }

    @media only screen and (max-width:480px) {
        .StoreList_Wrap{padding:1.5rem;}
    }

    @media only screen and (max-width:375px) {

    }
  </style>